import { uniPopup } from '@dcloudio/uni-ui'
export default {
	components: {
		uniPopup
	},
	data() {
		return {
			data: ''
		}
	},
	props: {
		title: {
			type: String
		}
	},
	mounted() {
		this.visible = this.value
	},
	methods: {
		submit() {
			let data = this.data
			this.$emit('submit', data)
			this.data = ''
		},
		close() {
			this.$refs.$unipopup.close()
		},
		open() {
			this.$refs.$unipopup.open()
		}
	}
}
